* {
	font-family: 'Roboto', Arial, sans-serif; }

body {
	position: relative;
	min-width: 320px;
	color: $darkGray;
	background: $white;
	font-family: 'Roboto', Arial, sans-serif;
	font-size: 14px;
	line-height: 1.3; }


html,
body,
#root {
	height: 100%; }

.main {
	flex-grow: 1;
	overflow: auto; }

.inner {
	padding: 40px 20px;
	margin: 0 auto;
	max-width: 600px; }

.section {
	&:not(:last-child) {
		margin-bottom: 40px; }
	p {
		font-size: 16px;
		line-height: 1.3;
		&:not(:last-child) {
			margin-bottom: 0.5em; } }
	&__label {
		margin-bottom: 10px; } }

#root {
	display: flex;
	flex-direction: column; }

[hidden] {
	display: none !important; }

.pre-line {
	white-space: pre-line; }

.text-center {
	text-align: center; }

.text-uppercase {
	text-transform: uppercase; }

.title-lg {
	@include f(20px, 700, 1.3);
	&:not(:last-child) {
		margin-bottom: 16px; } }

.title-md {
	@include f(16px, 700, 1.3);
	&:not(:last-child) {
		margin-bottom: 16px; } }
