html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-angle-down {
  width: 1.71em;
  height: 1em;
  fill: initial;
}

.icon-arrow-left {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-check {
  width: 1.3em;
  height: 1em;
  fill: initial;
}

.icon-close {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-question-option {
  width: 1em;
  height: 1em;
  fill: initial;
}

* {
  font-family: 'Roboto', Arial, sans-serif;
}

body {
  position: relative;
  min-width: 320px;
  color: #333;
  background: #fff;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

html,
body,
#root {
  height: 100%;
}

.main {
  flex-grow: 1;
  overflow: auto;
}

.inner {
  padding: 40px 20px;
  margin: 0 auto;
  max-width: 600px;
}

.section:not(:last-child) {
  margin-bottom: 40px;
}

.section p {
  font-size: 16px;
  line-height: 1.3;
}

.section p:not(:last-child) {
  margin-bottom: 0.5em;
}

.section__label {
  margin-bottom: 10px;
}

#root {
  display: flex;
  flex-direction: column;
}

[hidden] {
  display: none !important;
}

.pre-line {
  white-space: pre-line;
}

.text-center {
  text-align: center;
}

.text-uppercase {
  text-transform: uppercase;
}

.title-lg {
  font-size: 20px;
  font-weight: 700;
  line-height: 1.3;
}

.title-lg:not(:last-child) {
  margin-bottom: 16px;
}

.title-md {
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
}

.title-md:not(:last-child) {
  margin-bottom: 16px;
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  min-width: 120px;
  border: none;
  outline: none;
  color: #fff;
  background: #2e99db;
  font-family: inherit;
  font-size: 18px;
  font-weight: 300;
  line-height: 50px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s, background .175s, box-shadow .175s;
}

.is-metro .btn {
  background: #9784ff;
}

.is-boost .btn {
  background: #EF902D;
}

.btn:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.is-metro .btn:active {
  background: #6c51ff;
}

.is-boost .btn:active {
  background: #d97710;
}

.btn:disabled, .btn.is-disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.btn_gray {
  background: #bec1cb;
}

.is-metro .btn_gray {
  background: #bec1cb;
}

.is-boost .btn_gray {
  background: #bec1cb;
}

.btn_gray:active {
  background: #a2a6b4;
}

.is-boost .btn_gray:active {
  background: #a2a6b4;
}

.is-metro .btn_gray:active {
  background: #a2a6b4;
}

.btn_red {
  background: #ec4a41;
}

.is-metro .btn_red {
  background: #ec4a41;
}

.btn_red:active {
  background: #e32117;
}

.is-metro .btn_red:active {
  background: #e32117;
}

.btn_wide {
  width: 100%;
}

.btn_plus {
  width: 40px;
  min-width: auto;
  height: 40px;
}

.btn_plus:before, .btn_plus:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 18px;
  height: 1px;
  background: currentColor;
  transform: translate(-50%, -50%);
}

.btn_plus:after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.awesome-switcher {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin: 0;
  height: 30px;
  font-size: 0;
  cursor: pointer;
  user-select: none;
  appearance: none;
}

.awesome-switcher:active .awesome-switcher__control:before {
  box-shadow: inset 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.awesome-switcher__input {
  display: none;
}

.awesome-switcher__input:checked + .awesome-switcher__control {
  border-color: #2e99db;
}

.is-metro .awesome-switcher__input:checked + .awesome-switcher__control {
  border-color: #141547;
}

.is-boost .awesome-switcher__input:checked + .awesome-switcher__control {
  border-color: #EF902D;
}

.awesome-switcher__input:checked + .awesome-switcher__control:before {
  background: #2e99db;
}

.is-metro .awesome-switcher__input:checked + .awesome-switcher__control:before {
  background: #141547;
}

.is-boost .awesome-switcher__input:checked + .awesome-switcher__control:before {
  background: #EF902D;
}

.awesome-switcher__input:checked + .awesome-switcher__control:after {
  box-shadow: -1px 2px 2px rgba(0, 0, 0, 0.2);
  transform: translate3d(10px, 0, 0);
}

.awesome-switcher__control {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 50px;
  height: 100%;
  border-radius: 15px;
  border: 1px solid #ccc;
  transition: border-color 0.2s ease-in-out;
}

.awesome-switcher__control:before, .awesome-switcher__control:after {
  content: '';
  position: absolute;
}

.awesome-switcher__control:before {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 15px;
  background: #eee;
  transition: background 0.2s ease-in-out, box-shadow 0.1s ease-in-out;
}

.awesome-switcher__control:after {
  top: 50%;
  left: 50%;
  margin: -12px 0 0 -12px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  background: #fff;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  transform: translate3d(-10px, 0, 0);
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.select {
  padding: 0 30px 0 12px;
  border: 1px solid #cccccc;
  border-radius: 0;
  width: 100%;
  height: 40px;
  background: url(../img/angle-down.svg) no-repeat #fff;
  background-position: right 12px center;
  background-size: auto 6px;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.select::-ms-expand {
  display: none;
}

.select:not([readonly]):focus {
  border-color: #2e99db;
}

.is-metro .select:not([readonly]):focus {
  border-color: #9784ff;
}

.is-boost .select:not([readonly]):focus {
  border-color: #EF902D;
}

.select:disabled, .select[readonly] {
  border-color: #b3b3b3;
  background: #f2f2f2;
  cursor: not-allowed;
}

.select.is-error {
  border-color: #ec4a41;
}

.is-metro .select.is-error {
  border-color: #ec4a41;
}

.is-boost .select.is-error {
  border-color: #ec4a41;
}

.input {
  padding: 0 12px;
  border: 1px solid #cccccc;
  border-radius: 0;
  width: 100%;
  height: 40px;
  background: #fff;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.input::-ms-clear {
  display: none;
}

.input:not([readonly]):focus {
  border-color: #2e99db;
}

.is-metro .input:not([readonly]):focus {
  border-color: #9784ff;
}

.is-boost .input:not([readonly]):focus {
  border-color: #EF902D;
}

.input:disabled, .input[readonly] {
  border-color: #b3b3b3;
  background: #f2f2f2;
  cursor: not-allowed;
}

.input.is-error {
  border-color: #ec4a41;
}

.is-metro .input.is-error {
  border-color: #ec4a41;
}

.is-boost .input.is-error {
  border-color: #ec4a41;
}

.label {
  color: #666666;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
}

label[for] {
  cursor: pointer;
  user-select: none;
}

.value {
  color: #333;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

.value:not(:first-child) {
  margin-top: 16px;
}

.header {
  position: relative;
  flex-shrink: 0;
  color: #fff;
  background: #2e99db;
  text-align: center;
}

.is-metro .header {
  background: #141547;
}

.is-boost .header {
  background: #2e2e2e;
}

.header__close {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  font-size: 0;
  width: 60px;
  color: #fff;
  background: none;
  cursor: pointer;
  transition: background 0.25s, box-shadow 0.25s;
}

.header__close:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .header__close:hover {
  background: #1f7db7;
}

.no-touch.is-metro .header__close:hover {
  background: #141547;
}

.no-touch.is-boost .header__close:hover {
  background: #2e2e2e;
}

.header__close:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.is-metro .header__close:active {
  background: #141547;
}

.is-boost .header__close:active {
  background: #2e2e2e;
}

.header__close-icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  font-size: 20px;
}

.header__inner {
  padding: 0 60px;
  height: 60px;
  text-align: center;
  font-size: 0;
}

.header__inner:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@media only screen and (max-width: 500px) {
  .header__inner {
    padding: 10px 60px;
    height: auto;
    min-height: 60px;
  }
}

.header__title {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .header__title {
    font-size: 16px;
  }
}

.header__title span {
  text-transform: none;
}

.errors {
  font-size: 16px;
  line-height: 1.3;
}

.errors__item {
  position: relative;
  padding-left: 18px;
}

.errors__item:before {
  content: '';
  position: absolute;
  top: 7px;
  left: 0;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  background: currentColor;
}

.form-group:not(:last-child) {
  margin-bottom: 20px;
}

.form-group_inline {
  font-size: 0;
}

.form-group_inline .form-group__label, .form-group_inline .form-group__switcher {
  display: inline-block;
  vertical-align: top;
}

.form-group_inline .form-group__label {
  padding: 5px 16px 5px 0;
  margin: 0;
}

.form-group__label {
  margin-bottom: 8px;
}

.form-group__description, .form-group__control_inline {
  display: inline-block;
  vertical-align: top;
}

.form-group__control_inline {
  width: 180px;
}

.form-group__description {
  padding: 8px 16px 8px 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

.form-group__row {
  margin: -5px;
  font-size: 0;
}

.form-group__col {
  display: inline-block;
  vertical-align: top;
  padding: 5px;
  width: 50%;
}

@media only screen and (max-width: 580px) {
  .form-group__col {
    width: 100%;
  }
}

.check:not(:first-child) {
  margin-top: 16px;
}

.check__inner {
  background-color: #feffc6;
  background-image: linear-gradient(45deg, #fff 50%, transparent 50%), linear-gradient(-45deg, #fff 50%, transparent 50%);
  background-position: bottom left, bottom left;
  background-size: 19px 19px;
  background-repeat: repeat-x;
  font-size: 16px;
  line-height: 1.3;
  padding: 16px 16px 35px;
}

.grid {
  border: 1px solid #ccc;
}

.grid__header, .grid__row {
  display: flex;
}

@media only screen and (max-width: 500px) {
  .grid__header, .grid__row {
    flex-direction: column;
  }
}

.grid__header {
  color: #666666;
  background: #ccc;
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  border-bottom: 1px solid #ccc;
}

@media only screen and (max-width: 500px) {
  .grid__header {
    display: none;
  }
}

.grid__row {
  align-items: center;
}

@media only screen and (max-width: 500px) {
  .grid__row {
    align-items: inherit;
    padding: 15px;
  }
}

.grid__row:not(:last-child) {
  border-bottom: 1px solid #ccc;
}

.grid__column {
  padding: 10px;
  flex: 1 1 50%;
  font-size: 16px;
}

@media only screen and (max-width: 500px) {
  .grid__column {
    padding: 5px;
    flex-basis: 100%;
  }
}
